body{background-color:var(--background-color);color:var(--primari-text-color);font-family:Roboto,sans-serif}h1,h2,h3,h4,h5,h6{font-family:Comfortaa}img{display:block;max-width:100%;height:auto}ul{list-style:none}html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}*{margin:0;padding:0}a{text-decoration:none;color:inherit}.name{margin-bottom:10px;font-family:Roboto;font-style:normal;font-weight:500;font-size:16px;line-height:19px;letter-spacing:.03em}.container{padding-left:15px;padding-right:15px;margin-left:auto;margin-right:auto;width:1230px}.clients{padding-top:94px;padding-bottom:94px}.client-list{display:flex}.client-list-item{width:170px;height:90px;border:1px solid #afb1b8;border-radius:4px;transition-property:color,border;transition-duration:250ms;transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1)}.client-list-item{width:calc((100%-150px) / 6);margin-right:30px}.client-list-item:nth-child(6n){margin-right:0}.client-link{display:flex;width:100%;height:100%;justify-content:center;align-items:center;transition-property:color,border;transition-duration:250ms;transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1)}.client-logo{fill:#afb1b8}.client-list-item:hover{border-color:var(--accent-color)}.client-list-item:hover .client-logo{fill:var(--accent-color)}.client-link:focus{border-color:var(--accent-color)}.client-link:focus .client-logo{fill:var(--accent-color)}

